@import "../../global.scss";

.works{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
        margin-top: 70px;
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: center;

        @include mobile {
           flex-wrap: wrap;
           justify-content: center;
        }
    }

    .container{
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        flex-wrap: wrap;

        @include mobile {
            width: 100%;
         }

     .item{
        width: 220px;
        height: 150px;
        margin: 5px;
        border-radius: 20px;
        border: 1px solid rgb(240, 239, 239);
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        position: relative;
        transition: all 0.5s ease;
        cursor: pointer;

        @include mobile {
            width: 130px;
            height: 100px;
         }

        h3{
            position: absolute;
            font-size: 20px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }


        &:hover{
            background-color: $mainColor;
            img{
                opacity: 0.2;
                z-index: 0;
            }
        }
     }
}
}